body {
	font-family: 'Myriad Pro', 'PT Sans', sans-serif;
}

.footer {
	h5.ui-title {
		text-align:left;
	}
	.total {
		display:block;
		float:right;
	}
}

.header .navbar li {
	.departments, .need {
		padding:17px;
		position:relative;

		span {
			position: relative;
			z-index: 2;
		}
	}
	
	.pickup {
		text-align:left;
		padding-left:36px;
		padding-right:36px;
		.fa {
			display:block;
			float:right;
			margin-top:2px;
		}
	}

	.departments:before {
		position: absolute;
		font-family: 'FontAwesome';
		top: 50%;
		left: 50%;
		font-size:40px;
		margin-top:-24px;
		margin-left:-24px;
		color: #efefef;
		z-index: 1;
		content: "\f200";
	}

	.need:before {
		position: absolute;
		font-family: 'FontAwesome';
		top: 50%;
		left: 50%;
		font-size:40px;
		margin-top:-24px;
		margin-left:-24px;
		color: #efefef;
		z-index: 1;
		content: "\f080";
	}
}


/* round button */
.round-button {
	width:15%;
	position:fixed;
	bottom:60px;
	right:20px;
	z-index:2000;

	.round-button-circle {
		width: 100%;
		height:0;
		padding-bottom: 100%;
		border-radius: 50%;
		overflow:hidden;
		background-color:#e9e9e9;
		box-shadow: 1px 1px 2px grey;

		:hover {
			background:#30588e;
		}

		a, :hover a {
			display:block;
			float:left;
			width:100%;
			padding-top:50%;
			padding-bottom:50%;
			line-height:1em;
			margin-top:-0.5em;

			text-align:center;
			color:#fff;
			font-family:Verdana;
			font-size:2em;
			font-weight:bold;
			text-decoration:none;
		}
	}
}


.your-list-item {
	position:relative;
	
	.label {
		position:relative;
		z-index:10;
		display:table;
		width:100%;

		.row {
			display:table-row;
			
			div {
				display:table-cell;
				white-space: normal;
				vertical-align: middle;
			}
		}

		.icon {
			width:7%;
		}
		.quantity {
			width:17%;
		}
		.product {
			width:53%;
		}
		.price {
			width:23%;
			text-align:right;
		}
	}
	
	.need-bar {
		background-color:#efefef;
		height:100%;
		position:absolute;
		top:0;
		left:0;
		z-index:0;
	}
}